	import {
		basePath
	} from '@/utils/index'
	import api from "@/api/member"
	import {
		mapState
	} from 'vuex'
	import tuiIcon from "@/components/icon/icon"
	import tuiListCell from "@/components/list-cell/list-cell"
	import tuiLoadmore from "@/components/loadmore/loadmore"
	import tuiNomore from "@/components/nomore/nomore"
	import tuiTips from "@/components/tips/tips"
	import tuiButton from "@/components/button/button"
	export default {
		components: {
			tuiIcon,
			tuiListCell,
			tuiLoadmore,
			tuiNomore,
			tuiTips,
			tuiButton
		},
		computed: {
// 			count() {
// 				return this.newsList.length - 1
// 			}
		},
		data() {
			return {
				basePath,
				newsList: [],
				pageIndex: 1,
				lastPage:1,
				limit:1,
				loadding: false,
				pullUpOn: true
			}
		},
		computed: mapState(['forcedLogin', 'hasLogin', 'member_id']),
		onLoad: function(options) {
			//this.newsList = this.dataSources
			this.getFollow();
		},
		watch: {
			pageIndex(newValue, oldValue) {
				if(this.pageIndex==this.lastPage){
					console.log(22222)
					this.pullUpOn = false;
					
				}
			}
		},
		methods: {
			//查询关注
			getFollow(){
				var param = {
					page: this.$pagination.page,
					limit: this.$pagination.limit
				}
				var url=api.getFollow+'/'+this.member_id
				this.$postajax(url,param)
					.then(res => {
						if (res.code == 0) {
							this.newsList=res.data;
							this.lastPage =res.data&&res.data.length>0?(Math.ceil(res.count/this.$pagination.limit)):1;
							console.log("最后一页"+JSON.stringify(this.lastPage))
							if(this.pageIndex==this.lastPage){
								this.pullUpOn = false;
							}
							this.count=res.count;
						}
				
				
					})
					.catch(err => {
				
					});
			},
			detail(id) {
				uni.navigateTo({
					url: "../productDetail/productDetail?id="+id
				})
			},
			deleteFacorite(id,e) {
				/* e.stopPropagation();  */
				var url=api.closeFollow+'/'+id;
				this.$postajax(url)
				.then(res => {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						this.tui.toast("删除收藏成功", 2000, true);
						this.getFollow();
					}
			
			
				})
				.catch(err => {
			
				});
			}
		},
		//页面相关事件处理函数--监听用户下拉动作
		onPullDownRefresh: function() {
			//this.getFollow();
			//this.pageIndex = 1;
			this.pullUpOn = true;
			//this.loadding = false;
			this.loadding = true;
			console.log('上拉加载事件');
			// 每次拉到页面底部,请求页数自加
			this.pageIndex++;
			// 第二次之后请求数据
			if(this.pageIndex>this.lastPage){
				setTimeout(() => {
					this.loadding = false
					this.pullUpOn = false
				}, 1000)
				return false;
			}else{
				var param = {
					page: this.pageIndex,
					limit: this.$pagination.limit
				}
				var url=api.getFollow+'/'+this.member_id
				this.$postajax(url,param)
					.then(res => {
						console.log(JSON.stringify(res))
						if (res.code == 0) {
							this.newsList = this.newsList.concat(res.data)
							this.loadding = false;
						}
				
				
					})
					.catch(err => {
				
					});
							 
				 }
			
			uni.stopPullDownRefresh();
			let options = {
				msg: "刷新成功",
				duration: 2000,
				type: "translucent"
			};
			setTimeout(() => {
				this.$refs.toast.showTips(options);
			}, 300);
		},

		// 页面上拉触底事件的处理函数
		onReachBottom: function() {
			this.pullUpOn = true;
			//this.loadding = false;
			this.loadding = true;
			console.log('上拉加载事件');
			// 每次拉到页面底部,请求页数自加
			this.pageIndex++;
			// 第二次之后请求数据
			if(this.pageIndex>this.lastPage){
				setTimeout(() => {
					this.loadding = false
					this.pullUpOn = false
				}, 1000)
				return false;
			}else{
				var param = {
					page: this.pageIndex,
					limit: this.$pagination.limit
				}
				var url=api.getFollow+'/'+this.member_id
				this.$postajax(url,param)
					.then(res => {
						console.log(JSON.stringify(res))
						if (res.code == 0) {
							this.newsList = this.newsList.concat(res.data)
							this.loadding = false;
						}
				
				
					})
					.catch(err => {
				
					});
							 
				 }
			
			uni.stopPullDownRefresh();
			let options = {
				msg: "刷新成功",
				duration: 2000,
				type: "translucent"
			};
			setTimeout(() => {
				this.$refs.toast.showTips(options);
			}, 300);
		}
	}
